<template>
<div id="rentCar">
   <h2>商家如何录入车辆？</h2>
  <p>在工作台点击车辆录入，完成车辆录入操作，完成车辆录入后用户才可以取车。</p>
  <p>首先选择车辆要录入的商品，根据长租/短租选择不同的商品。第二步通过手动输入或扫码录入车辆SN，同一批SN录入同一个商品，可依次扫码录入。</p>
  <p>商品若录入错误，可能导致用户无法取车，需要在更改商品处进行更改。用户取车前请商家确认车辆已录入、车辆已经放入电池。</p>
  <div class="imgDiv">
    <img src="../../assets/m5.png" alt="">
  </div>

</div>
</template>

<script>
    export default {
        name: "rentCar"
    }
</script>

<style scoped lang="scss">
#rentCar{
 padding: 0 40px;
  box-sizing: border-box;
  h2{
    font-size:28px;
    font-family:PingFangSC-Medium;
    font-weight:500;
    color:rgba(51,51,51,1);
    line-height: 100px;
    border-bottom: 1px solid #F0F0F0;
    text-align: left;
  }
  p{
    font-size:28px;
    font-family:PingFangSC-Regular;
    font-weight:400;
    color:rgba(72,72,72,1);
    line-height:40px;
    margin-top: 38px;
    text-align: left;
  }
  .imgDiv{
    width:400px;
    margin: 40px auto;
    margin-top: 70px;
    img{
      height: 100%;
      width: 100%;
    }
  }
}
</style>



